.landing-container {
  min-height: 100vh;
  background: linear-gradient(180deg, #f3e8ff 0%, #e0f2fe 100%);
  position: relative;
  overflow: hidden;
}

.landing-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  padding: 2rem;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #1e293b;
  font-family: 'SF Pro Display', 'SF Pro Text', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
}

.logo-icon {
  color: #f59e0b;
  width: 24px;
  height: 24px;
}

.landing-main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  min-height: 100vh;
  padding: 0 4rem;
  text-align: left;
  position: relative;
  z-index: 2;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.hero-content {
  max-width: 800px;
  margin: 0;
}

.hero-title {
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 400;
  background: linear-gradient(135deg, #ec4899 0%, #8b5cf6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1.5rem;
  line-height: 1.2;
  font-family: 'Playfair Display', serif;
  letter-spacing: -0.01em;
  transition: all 0.3s ease;
  cursor: pointer;
  padding-bottom: 0.2rem;
}

.hero-title:hover {
  background: linear-gradient(135deg, #0ea5e9 0%, #10b981 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gradient-text {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 400;
}

.hero-subtitle {
  font-size: clamp(1rem, 2.5vw, 1.4rem);
  background: linear-gradient(135deg, #ec4899 0%, #8b5cf6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 5rem;
  margin-top: 2rem;
  font-weight: 400;
  font-family: 'SF Pro Display', 'SF Pro Text', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  transition: all 0.3s ease;
  cursor: pointer;
}

.hero-subtitle:hover {
  background: linear-gradient(135deg, #0ea5e9 0%, #10b981 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-description {
  max-width: 500px;
  margin: 0 0 4rem 0;
  color: #64748b;
  font-size: 1.1rem;
  line-height: 1.6;
  font-weight: 400;
}

.hero-description p {
  margin-bottom: 0.5rem;
}

.scroll-indicator {
  background: none;
  border: 2px solid #cbd5e1;
  color: #64748b;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 2rem;
  align-self: center;
}

.sparkles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.sparkle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  box-shadow: 0 0 6px rgba(255, 255, 255, 0.8);
}



.scroll-indicator:hover {
  border-color: #667eea;
  color: #667eea;
  background: rgba(102, 126, 234, 0.05);
  transform: translateY(5px);
}

.bg-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  pointer-events: none;
}

.floating-shapes {
  position: relative;
  width: 100%;
  height: 100%;
}

.shape {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.shape-1 {
  width: 200px;
  height: 200px;
  top: 15%;
  right: 10%;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.08) 0%, rgba(118, 75, 162, 0.08) 100%);
}

.shape-2 {
  width: 150px;
  height: 150px;
  bottom: 20%;
  left: 8%;
  background: linear-gradient(135deg, rgba(245, 158, 11, 0.1) 0%, rgba(251, 191, 36, 0.1) 100%);
}

.shape-3 {
  width: 100px;
  height: 100px;
  top: 40%;
  left: 15%;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.08) 0%, rgba(168, 85, 247, 0.08) 100%);
}

/* Responsive design */
@media (max-width: 768px) {
  .landing-header {
    padding: 1.5rem;
  }
  
  .landing-main {
    padding: 0 1.5rem;
  }
  
  .hero-description {
    font-size: 1rem;
    margin-bottom: 3rem;
  }
  
  .shape-1 {
    width: 120px;
    height: 120px;
    right: 5%;
  }
  
  .shape-2 {
    width: 100px;
    height: 100px;
    left: 5%;
  }
  
  .shape-3 {
    width: 80px;
    height: 80px;
    left: 10%;
  }
}

@media (max-width: 480px) {
  .hero-title {
    margin-bottom: 1rem;
  }
  
  .hero-subtitle {
    margin-bottom: 2rem;
  }
  
  .hero-description {
    margin-bottom: 2rem;
  }
}
